<template>
<FrontNavbar class="FrontNavbar" />
  <div class="login-container">
    <div class="row justify-content-center">
      <div class="login-top-space"></div>
      <h1 class="h3 mb-3 font-weight-normal">
        請先登入
      </h1>
      <div class="col-8">
        <form id="form" class="form-signin" @submit.prevent="login">
          <div class="form-floating mb-3">
            <input type="email" class="form-control" id="username"
            placeholder="name@example.com" v-model="user.username" required autofocus >
            <label for="username">Email address</label>
          </div>
          <div class="form-floating">
            <input type="password" class="form-control" id="password"
            placeholder="Password" v-model="user.password"  required >
            <label for="password">Password</label>
          </div>
          <button class="btn btn-lg btn-primary w-100 mt-3" type="submit" >
            登入
          </button>
        </form>
      </div>
    </div>
    <p class="mt-5 mb-3 text-muted">
    &copy; 2021~∞ - 六角學院
    </p>
  </div>
<FrontFooter />
</template>

<style>
.login-top-space {
  height: 6rem;
}

.login-container h1 {
  text-align: center;
}

.login-container p {
  text-align: center;
}
</style>

<script>
import FrontNavbar from '@/components/FrontNavbar.vue'
import FrontFooter from '@/components/FrontFooter.vue'
export default {
  data () {
    return {
      user: {
        username: '',
        password: ''
      }
    }
  },
  components: {
    FrontNavbar,
    FrontFooter
  },
  methods: {
    login () {
      this.$http.post(`${process.env.VUE_APP_API}/admin/signin`, this.user)
        .then((res) => {
          const { token, expired } = res.data
          document.cookie = `hexToken=${token}; expires=${new Date(expired)};`
          this.$router.push('/Admin/AdminProducts')
        })
        .catch((err) => {
          this.$httpMessageState(err.response, '登入')
        })
    }
  }
}
</script>
